.releases{
    display: grid;
    place-items: center;
    @include tablet{
        @include grid(2, $separacion/4);
    }
    @include desktop{
        @include grid(3, $separacion/4);
    }
    @include hd{
        @include grid(4, $separacion/4);
    }    
    .release{
        width: 28rem;
        .release__img{            
            height: 36rem;
            background-color: $blanco;
            box-shadow: 0 0 4px rgba(0,0,0,.2);
            border-radius: .5rem;
            position: relative;
            overflow: hidden;
            transition: all .5s ease-in-out;
            display: grid;
            place-items: center ;
            &::before{
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                top: -50%;
                background-color: $primario;
                transform: skewY(320deg);
                transition: all .5s ease-in-out;
            }
            &:hover::before{
                background-color: $secundario;
                transform: skewY(400deg);
            }
            img {
                position: relative;
                transition: all .5s ease-in-out;
            }
            &:hover{
                img{
                    width: 80%;
                   
                }
                .addToBag{
                    bottom: 0;
                }
            }
            .addToBag{
                position: absolute;
                left: 0;
                right: 0;
                bottom: -20rem;
            }
        }
        .release__info{
            padding: 1rem;
            h3{
                font-size: 2.5rem;
                text-align: left;
                line-height: .9;
            }
            p {
                font-size: 1.6rem;
                text-align: left;
                line-height: 1.5;
                span{
                    font-weight: $bold;
                }
            }
            p.price{
                font-size: 2.5rem;
                color: $secundario;
                font-family: $titulos;
                font-weight: $semi;
            }
        }
    }
}
.button{
    margin: $separacion/2 auto;
}