body.login{
    background: linear-gradient(30deg, rgba(223,223,223,1) 0%, rgba(184,184,184,1) 50%, rgba(200,3,13,1) 50%, rgba(93,2,7,1) 100%);
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.formulario {
    background-color: rgba(255,255,255,1);
    width: 36rem;
    padding: 3rem;
    box-shadow: 0 .5rem 3.5rem rgba(0,0,0,0.2);
    border-radius: 1rem;
    @include tablet {
        width: 50rem;
    }
    h2{
        text-align: center;
        margin-bottom: 3rem;
        font-size: 3rem;
        letter-spacing: .2rem;
        font-weight: 300;
    }
    .home{
        display: block;
        font-size: 2rem;
        padding:1.5rem 0;
        text-align: center;
        &:hover{
            color: $secundario;
        }
    }
    .opciones{ 
        @include tablet{
            display: flex;
            justify-content: space-between;
        }       
        p{
            font-size: 1.5rem;
            cursor: pointer;
            span {
                text-decoration: underline;
                &:hover{
                    color:$secundario;
                }
            }
        }
    }
}
.campo{
   margin-bottom: 2rem;
   label{
    display: block;
    font-size: 2rem;
    font-weight: 300;
    position: relative;   
    }
    input{
        width: 100%;
        padding: 1.5rem 4rem 1.5rem 1rem;    
        border: none;
        outline: red;
        font-size: 2rem;
        border-bottom: .1rem solid #5e5e5e3b;
        border-radius: 1rem;
        background-color: rgba(0,0,0,.05);
        
    }
}
.input{
    position: relative;
    &::after{
        position: absolute;
        display: grid;
        place-items: center;
        text-align: right;
        font-size: 3rem;
        font-family: "Font Awesome 5 Free";
        color:rgba(0,0,0,.3);
        font-weight: 900;
        top:0;
        right: 1rem;
        bottom: 0;    
        
    }
}
.input.usuario::after{
    content: '\f007';
}
.input.password::after{
    content: '\f084';
}

input[type="submit"]{
    display: block;
    width: 100%;
    border: .1rem solid #5e5e5e3b;
    border-radius: 1rem;
    padding: 1rem 3rem;
    font-weight: 400;
    cursor: pointer;
    text-transform: uppercase;
    transition: .3s all ease-in-out;
    margin-top: 5rem;
}
input[type="submit"]:hover{
    background-color: $secundario;
}
