.hero{   
    display: flex;
    flex-direction: column;    
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    background-image: url('../../../build/img/modelos.jpg');
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center 5rem;
    position: relative;
    
    // background: rgba(255,255,255,1);
    // background: -moz-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(246,246,246,1) 47%, rgba(242,242,242,1) 100%);
    // background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(255,255,255,1)), color-stop(47%, rgba(246,246,246,1)), color-stop(100%, rgba(242,242,242,1)));
    // background: -webkit-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(246,246,246,1) 47%, rgba(242,242,242,1) 100%);
    // background: -o-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(246,246,246,1) 47%, rgba(242,242,242,1) 100%);
    // background: -ms-radial-gradient(center, ellipse cover, rgba(255,255,255,1) 0%, rgba(246,246,246,1) 47%, rgba(242,242,242,1) 100%);
    // background: radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(246,246,246,1) 47%, rgba(242,242,242,1) 100%);
    // filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f2f2f2', GradientType=1 ); 

    @include dosk{
        min-height: 45vh;
    }
    &::before{
        content: '';
        width: 100%;
        height: 100%;        
        top: 0;
        left: 0;
        position: absolute;
        background: rgb(104,160,105);
        background: linear-gradient(180deg, rgba(104,160,105,0.30) 15%, rgba(104,160,105,1) 100%);
    }
}
.hero-content{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1;            
    .hero__left{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        z-index: 1;      
        h1{
            font-size: 6rem;
            text-align: center;
            line-height: 0.8;
            margin-bottom: 2rem;
            color: $blanco;
            text-shadow: 0px 1px 3px rgba(0, 0, 0, .75);            
            @include tablet{
                font-size: 10rem;
            }
            @include desktop{
                font-size: 12vw;                
            }           
            span {
                display: block;
                font-size: 4.5rem;
                font-weight: $light;
                @include tablet{
                    font-size: 7.5rem;
                }
                @include desktop {
                    font-size: 9vw;
                }
            }
        }
        p.slogan{
            font-weight: $normal;
            line-height: 1;
            text-align: center;
            font-size: 3rem;
            
        }
        .button{
            margin-top: 10rem;
            @include desktop{
                margin: 8rem auto 0 auto;
            }
        } 
    }
    .hero__right{ 
        padding: 4rem 0; 
        height: 100%;
        width: 100%;
        display: flex;                              
        justify-content: center;
        align-items: center;
        @include tablet{
            padding: 0;
        }    
        img{
            width: 22rem; 
            height: 22rem;
            @include tablet{
                width: 50rem;
                height: 48rem;                
            }
            @include desktop{
                width: 60rem;
                height: 60rem;
                transform: translateX(-6rem);
            }
        }
    }
}