.bolsa__compras{
    ul{
        li{
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 1rem;
            @include telefono{
                flex-direction: row;
            }
            img{
                width: 15rem;
                padding: 1rem;
            }
            .item__info{
                @include telefono{
                    text-align: left;
                }
                h3{
                    font-size: 1.8rem;
                }
                p{
                    line-height: 1.2;
                }
                .precio{
                    font-weight: $semi;
                }
            }

        }
    }
    .botones{
        @include tablet {
            display: flex;
        }
        
    }
}