.container{
    width: 96%;
    max-width: 130rem;
    margin: 0 auto;
}
.no-scroll{
    overflow: hidden;
}
.text-center{
    text-align: center;
}
.button{
    position:relative;
    height: 6rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;    
    color: $blanco;
    font-family: $titulos;
    font-weight: 500;
    font-size: 2.5rem;
    text-align: center;
    cursor: pointer;
    border-radius: .5rem;
    transition: all .3s ease-in-out;
    -webkit-box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.5); 
    box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.5);   
    @include telefono {
        width: 50%;
        margin: 0 auto;
    }
    @include tablet{
        width: 20rem;        
    }
    @include desktop{
        width: 30rem;
        
    }
}
.btn-primary{
    background-color: $primario;
    &:hover{
        background-color: $primarioOscuro;
    }
}
.btn-secundary{
    background-color: $secundario;
    border-radius: 3.5rem;
    font-family: $contenido;
    font-weight: $light;
    font-size: 1.8rem;
    height: 5rem;
    width: 70%;
    margin: 0 auto;
    &:hover{
        background-color: $secundarioClaro;
    }
}
.new-section{
    padding-top: $separacion/2;
}
.titulo{
    text-transform: uppercase;
    margin-bottom: $separacion/2;
    position: relative;
    display: inline-block;
    font-size: 2.4rem;
    &::before{
        content: '<';
        position: absolute;
        top: 0;        
        left: -1rem;
        color: $primario;
        font-size: 2rem;
    }
    &::after{
        content: '/>';       
        position: absolute;
        top: 0;
        right: -2rem;
        color: $primario;
        font-size: 2rem;
    }
}
.logo{
    text-transform: uppercase;
    font-weight: $bold;
    line-height: 1;
    color: $primario;
    
    @include tablet{
        font-size: 4rem;
    }
    span {
        display: none;
        font-weight: $light;
        color: $secundario;
        @include telefono{
            display: inline;
        }
    }
}

