.eligenos-contenido{
    display: grid;
    gap: 2rem;
    padding-bottom: $separacion/4;
   @include tablet{
        display: grid;
        grid-template-columns: 60% 40%;
   }
    @include desktop{
        grid-template-columns: 60% 40%;
    }
    .beneficios{
        @include tablet{
            @include grid(2, $separacion/8);
        }
        @include desktop{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
        .beneficio{
            margin-bottom: 2rem;
            @include tablet{
                display: flex;
                flex-direction: column;
                justify-content: center;                
            }            
            @include desktop{
                display: grid;
                grid-template-columns: 10% 80%;
                gap: 2rem;
            }           
            .beneficio__img{
                margin-bottom: 1rem;
                @include desktop{
                    margin: 0;
                }
                i{
                    color: $secundario;
                    font-size: 5rem;
                    text-align: center;
                }
            }
            .beneficio__contenido{
                text-align: center;
                @include desktop{
                    text-align: left;                   
                }
                h3{
                    text-transform: uppercase;
                    margin-bottom: 1rem;
                    @include tablet {
                        font-size: 2rem;
                    }                    
                }
                p{
                    line-height: 1;
                    @include tablet {
                        font-size: 1.6em;
                    }
                }                
            }
            
        }
    }
    .eligenos-img{
        display: flex;
        overflow: hidden;
        background-color: brown;
        border-radius: .5rem;
        
    }
}