.description{
    font-size: 2.5rem;
}
.staff{
    padding: $separacion/4 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet{
       flex-direction: row;
       justify-content: center;
    }    
    .member{
        width: 22rem;
        height: 30rem;
        padding: 2rem 1rem;
        margin-bottom: 2rem;
        :last-of-type{
            margin-bottom: 0;
        }
        .member_img{
            width: 20rem;
            height: 20rem;
            margin-bottom: 1rem;            
            display: flex;
            justify-content: center;
            overflow: hidden;
            border-radius: 50%;
            border: 1rem solid $blanco;
            box-shadow: 0 0 4px  rgba(0,0,0,.5);
            img{
                height: 100%;
                width: auto;
                filter: grayscale(1);
                transition: all .5s ease-in-out;
                &:hover{
                    filter: grayscale(0);
                    transform: scale(1.1);
                }
                
            }
        }
        p{
            line-height: 1;
            font-size: 1.6rem;
        }
    }
}            