.buydetails{
    @include tablet{
        min-height: 100vh;
    }
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.details{
    flex: 1;
    height: 100%;
    .product{
        display: flex;
        justify-content: center;
        flex-direction: column;
        @include tablet{
            flex-direction: row;
            padding-top:5rem ;
        }
        .product__name{
            margin-bottom: 2rem;
            flex: 1;
        }
        .product__img{
                       
            @include tablet{
                padding: 0 1.5rem;
            }
            img{
                width: 30rem;
            }
            h3{
                text-align: left;
                color: $secundario;
                @include tablet{
                    text-align: center;
                }
            }
        }
        .product__info{
            padding: 1.5rem 0;
            @include tablet{
                padding: 0 1.5rem;
            }
        }
    }
}
.add__form{
    .campo{
        @include grid(2, 1rem);
        grid-template-columns: 25% 65%;
        place-items: center left; 
        text-align: left;
        margin-bottom: 2rem;
        .campo__label{
            margin-right: 2rem;
        }
        .tallas{
            display: flex;
            justify-content: space-between;
            width: 100%;
            .talla{
                label{
                    display: flex;
                    justify-content: center;
                    align-items: center;                    
                    width: 2rem;
                    height: 2rem;
                    padding: 2rem;
                    border: .1rem solid #e1e1e1;
                    border-radius: .2rem;
                    cursor: pointer;                    
                }                
            }
            input[type="radio"]{
                display: none;
            }
        }
        select{
            flex: 1;            
        }
        select, input[type="number"]{
            flex: 1;
            border: .1rem solid #e1e1e1;
            border-radius: .5rem;
            font-size: 2rem;
            padding: 1.5rem 1rem;
            font-family: $contenido;
            font-weight: $light;
            background-color: transparent;
            &:focus{
                border-color: $secundario;
            }
        }
        input[type="number"]{
            width: 10rem;
            text-align: center;
        }
    }
    .precio{
        text-align: left;
        font-weight: $light;
        font-size: 2.5rem;
        span {
            font-weight: $semi;
            color: $primario;
        }
    }
    input[type="submit"]{
        margin: 1rem 0 0 0;
    }    
}
input[name="talla"]:checked ~ label{
    background-color: $secundario;
    font-weight: $normal;
    color: $blanco;
}

.mensaje{
    background-color: yellow;
    padding: 1rem 0;
    text-align: center;
    color: $blanco;
    font-weight: $light;
    border-radius: .5rem;
}
.error{
    background-color: #ff3c3c;
}
.exito{
    background-color: $secundario;
}