.social-networks {
  display: flex;
  list-style: none;
  position: relative;
  margin: 0 auto; }
  .social-networks li {
    margin: 0 2rem;
    cursor: pointer;
    position: relative; }
    .social-networks li a {
      text-decoration: none; }
      .social-networks li a .fab {
        color: #1C1C1C;
        font-size: 4rem; }
    .social-networks li::before {
      position: absolute;
      top: 0rem;
      left: 0;
      font-family: "Font Awesome 5 Brands";
      font-size: 4rem;
      height: 0;
      transition: 0.5s ease-in-out;
      color: #ff4646;
      overflow: hidden; }
    .social-networks li:hover::before {
      height: 100%; }
    .social-networks li:nth-child(1)::before {
      content: '\f082';
      color: #1876f2;
      border-bottom: .4rem solid #1876f2; }
    .social-networks li:nth-child(2)::before {
      content: '\e055';
      border-bottom: .4rem solid #d6249f;
      background: #d6249f;
      background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
      background-clip: text;
      -webkit-text-fill-color: transparent; }
    .social-networks li:nth-child(3)::before {
      content: '\f081';
      color: #1da1f2;
      border-bottom: .4rem solid #1da1f2; }

