.input {
  position: relative; }
  .input::after {
    position: absolute;
    display: grid;
    place-items: center;
    text-align: right;
    font-size: 3rem;
    font-family: "Font Awesome 5 Free";
    color: rgba(0, 0, 0, 0.3);
    font-weight: 900;
    top: 0;
    right: 1rem;
    bottom: 0; }

.input.usuario::after {
  content: '\f007'; }

.input.password::after {
  content: '\f084'; }

